<template>
	<view class="box">
		<view class="goods">
			<view class="type-list">
				<view class="left">
					<scroll-view id="tab-bar" class="scroll-h" :scroll-x="true" :show-scrollbar="false">
					    <view v-for="(tab,index) in cate_list" :key="index" class="type-li uni-tab-item" :id="index" :data-current="index" @click="chooseCate(tab.catCode)">
					        <text class="uni-tab-item-title" :class="tab.catCode==choose_cate ? 'active uni-tab-item-title-active' : ''">{{tab.catName}}</text>
					    </view>
					</scroll-view>
				</view>
			</view>
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
				<view class="goods-box">
					<template v-for="(item,k) in data_list">
						<navigator class="goods-li" :url="'/pages/goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId"
						 :key="k">
							<image lazy-load class="image-wrapper img" :class="[item.loaded]" @load="onImageLoad('data_list',k)" :src="item.mainPicUrl" mode="aspectFill"></image>
							<view class="title-box">
								<view class="tips-box" v-if="data_list[k].spuLabel">
									<template v-for="(val,i) in data_list[k].spuLabel">
										<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
											{{val}}
										</view>
									</template>
								</view>
								<view class="title">
									{{item.spuTitle}}
								</view>
								<view class="price-box">
									<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
									<!-- <view class="people-num">1001人付款</view> -->
								</view>
							</view>
						</navigator>
					</template>
				</view>
			</mescroll-body>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				upOption: {
					textNoMore: "没有更多数据",
				},
				search_text: "",
				spuTitle: "",
				data_list: [],
				cate_list: [{
					catCode: "-1",
					catLevel: 1,
					catName: "全部",
					createBy: "",
					createDate: "",
					merchantSeqId: "",
					parentCode: "0",
					picUrl: "",
					remarks: "",
					seqId: "",
					sort: 1,
					status: "SHOW",
					typeValue: null,
					updateBy: "",
					updateDate: "",
				}],
				choose_cate: -1,
			}
		},
		components: {
			MescrollBody
		},
		onLoad() {
			this.getCate()
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pCatCode: this.choose_cate == '-1' ? "" : this.choose_cate,
						pageno: pageNum,
						pagesize: pageSize,
						promptType: 'NONE', //促销类型
						purchaseType: 'ZC', //购买类型
						onSale: 'SALE'
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for (let val of curPageData) {
						if (val.spuLabel) {
							val.spuLabel = val.spuLabel.split(',')
						}
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			chooseCate(id) {
				this.choose_cate = id;
				this.data_list = [];
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0)
			},
			getCate() {
				this.$api.get('/o2oGoodsCategory/findCategoryLevel1').then((res => {
					if (res.code == 200) {
						let data=res.data;
						this.cate_list = this.cate_list.concat(data)
					}
				}))
			},
			onImageLoad(item,k){
				this.$set(this[item][k], 'loaded', 'loaded');
			},
		}
	}
</script>

<style lang="scss">
	.scroll-h {
		flex-direction: row;
		/* #ifndef APP-PLUS */
		white-space: nowrap;
		/* #endif */
	}
	
	.uni-tab-item {
		/* #ifndef APP-PLUS */
		display: inline-block;
		/* #endif */
		flex-wrap: nowrap;
	}
	
	.uni-tab-item-title {
		flex-wrap: nowrap;
		/* #ifndef APP-PLUS */
		white-space: nowrap;
		/* #endif */
	}
	
	.scroll-area {
		width: calc(100% - 68rpx);
		height: 160rpx;
		margin: 34rpx auto;
		flex-direction: row;
		/* #ifndef APP-PLUS */
		white-space: nowrap;
		/* #endif */
	}
	.type-list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 20rpx auto;
		margin-bottom: 40rpx;
	
		.left {
			width:100%;
			flex-shrink: 0;
			font-size: 26rpx;
			position: relative;
			z-index: 2;
	
			.cate-box {
				width: 100%;
				display: flex;
				overflow: auto hidden;
			}
	
			.type-li {
				height: 40rpx;
				line-height: 40rpx;
				flex-shrink: 0;
				position: relative;
				overflow-y: hidden;
				display: inline-block;
				text-align: center;
				margin-right: 40rpx;
			}
	
			.active {
				font-size: 30rpx;
				font-weight: bold;
	
				&::after {
					content: "";
					position: absolute;
					bottom: 0;
					left: 10%;
					width: 80%;
					height: 12rpx;
					background: $color-primary;
					border-radius: 6rpx;
					z-index: -1;
				}
			}
		}
	
	}



	.goods {
		width: calc(100% - 68rpx);
		height: auto;
		margin: 20rpx auto;
	}

	.search-btn {
		border: 1px solid #157658;
		border-radius: 18px;
		font-size: 12px;
		font-weight: bold;
		color: #157658;
		text-align: center;
		width: 100rpx;
		height: 60rpx;
		line-height: 60rpx;
		background: $color-primary;
		color: white;
		flex-shrink: 0;
		flex-grow: 0;
	}

	.search-box {
		width: calc(100% - 68rpx);
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 24rpx auto;

		.left {
			width: 400rpx;
			height: 64rpx;
			background: white;
			border-radius: 32rpx;
			align-items: center;
			display: flex;
			padding: 0 70rpx;
			overflow: hidden;
			color: #959595;
			font-size: 24rpx;

			.iconfont {
				font-size: 30rpx;
			}

			.serarch {
				flex-grow: 1;
				height: 64rpx;
				font-size: 24rpx;
				margin-left: 20rpx;
			}
		}

		.iconshouye-xiaoxi {
			font-size: 36rpx;
			color: #959595;
			position: relative;

			.dot {
				width: 14rpx;
				height: 14rpx;
				background: $color-primary;
				border-radius: 100%;
				position: absolute;
				top: -4rpx;
				right: -6rpx;
				z-index: 1;
			}
		}
	}

	.goods-box {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		.goods-li {
			flex-shrink: 0;
			width: 332rpx;
			background: rgba(255, 255, 255, 1);
			border-radius: 8rpx;
			overflow: hidden;
			margin-bottom: 34rpx;

			.img {
				width: 332rpx;
				height: 440rpx;
				background: white;
			}
		}
	}

	.width106 {
		height: auto;
		font-size: 20rpx;
		margin-right:10rpx;
		padding: 6rpx 14rpx;
	}

	.title-box {
		width: calc(100% - 40rpx);
		padding: 20rpx 20rpx;
		padding-top: 0;

		.tips-box {
			display: flex;
		}

		.title {
			font-size: 30rpx;
			margin: 5rpx 0;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;

		}

		.price-box {
			width: 100%;
			display: flex;
			align-items: center;

			.num {
				font-size: 32rpx;
				color: $color-primary;
			}

			.people-num {
				font-size: 20rpx;
				margin-left: 40rpx;
			}
		}
	}
</style>
